@use '../../consts.scss' as *;

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
	margin: 0;

	height: calc(100vh - 0.5rem);

	user-select: none;
	background-color: $bg;
	#root {
		height: 100%;
	}
	.app {
		height: 100%;
		font-family: 'Poppins', sans-serif;
		font-weight: 500;
		color: $fg;

		.widget {
			width: 2.5rem;
			height: 2.5rem;

			padding: 0.5rem;
			margin: 0.5rem;
			background-color: $bg;
			border-radius: 100%;
			cursor: pointer;
			& > * {
				width: 2.5rem;
				height: 2.5rem;
				filter: invert(41%) sepia(64%) saturate(1184%)
					hue-rotate(356deg) brightness(83%) contrast(94%);
			}
		}
	}
}
*::-webkit-scrollbar {
	width: 1rem;
}
*::-webkit-scrollbar-track {
	border-radius: 1rem;
	background-color: darken($bg, 10);
}
*::-webkit-scrollbar-thumb {
	border-radius: 1rem;
	background-color: $fg;
}

button,
input {
	outline: none;
	border: none;
	background-color: unset;
	font-family: unset;
	color: unset;
	font-weight: unset;
}
button {
	padding: 0.5rem;
	font-size: 125%;
	font-weight: 300;

	color: $bg;
	background-color: $fg;
	border-radius: 0.5rem;

	cursor: pointer;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}
