@use '../../../consts.scss' as *;

.selector {
	display: flex;
	flex-direction: row;

	margin: 0.5rem;
	border-radius: 1rem;
	overflow: hidden;
	background-color: darken($bg, 5);
	.field {
		flex: 1;
		text-align: center;
		padding: 0.5rem;
		&[pick] {
			color: $bg;
			background-color: $fg;
		}
	}
}
