@use '../../../consts.scss' as *;

.checkbox {
	width: 1em;
	height: 1em;
	aspect-ratio: 1/1;

	font-size: 250%;

	background-color: darken($bg, 10);
	border-radius: 0.25em;
	overflow: hidden;
	& > * {
		width: 100%;
		height: 100%;
		aspect-ratio: 1/1;
		&.checked {
			background-color: $fg;
		}
		> * {
			aspect-ratio: 1/1;
			width: 100%;
			height: 100%;
			transform: translateY(-0.1em);
		}
	}
}
