@use '../../../../consts.scss' as *;

.diagrams {
	margin: 1.5rem;
	.top-bar {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
		.widget {
			background-color: darken($bg, 5);
		}
	}
	.list {
		height: calc(100vh - 15rem);
		overflow-y: scroll;
		.problem {
			display: flex;
			flex-direction: row;
			justify-content: space-between;

			margin: 0.5rem;
			padding: 0.5rem;

			background-color: darken($bg, 7);
			border-radius: 1rem;
			transition: 0.3s ease-out;
			&:hover {
				background-color: mix(darken($bg, 7), $fg, 70);
			}
			.eq {
			}
			.amt {
			}
		}
	}
}
