@use '../../../../consts.scss' as *;

.home-page {
	margin: 0.5rem;
	height: calc(100% - 8rem);

	padding: 1rem 0;

	background-color: darken($bg, 10);
	border-radius: 1rem;
	.items {
		display: flex;
		flex-direction: row;
		align-items: flex-start;
		justify-content: space-around;
		flex-wrap: wrap;
		.item {
			display: flex;
			flex-direction: row;
			align-items: flex-start;

			margin: 0.5rem;
			padding: 0.5rem;

			width: calc(50% - 2rem);
			min-width: 25rem;
			height: min-content;
			text-overflow: ellipsis;

			border-radius: 1rem;
			box-shadow: 0.15rem 0.15rem 0.5rem #000;
			cursor: pointer;
			.icon {
				font-size: 400%;
				width: 2.5rem;
				margin: 0.5rem;
			}
			.info {
				.title {
					font-size: 150%;
				}
				.subtitle {
					font-size: 110%;
				}
			}
		}
	}
}
