@use '../../../../consts.scss' as *;

.challenge {
	.box {
		transform: scale(2);
		text-align: center;
		.inside {
			display: flex;
			flex-direction: row;
			align-items: center;
			.guess {
				width: 5rem;
				height: min-content;

				font-size: 200%;
				border-bottom: 1px solid $fg;

				-moz-appearance: textfield;
				appearance: textfield;
				&::-webkit-outer-spin-button,
				&::-webkit-inner-spin-button {
					-webkit-appearance: none;
					margin: 0;
				}
			}
		}
	}
}
.bar {
	position: relative;

	width: 100%;
	height: 1rem;

	background-color: darken($bg, 25);
	border-radius: 1rem;
	&.bar-reducing {
		width: 100%;
		&::after {
			width: 100%;
			transition: width 0.1s ease;
			animation: gotozero var(--t) linear;
		}
	}
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;

		width: var(--p);
		height: 1rem;

		background-color: $fg;
		border-radius: 1rem;
	}
}
.result {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	.inside > * {
		margin: 0.5rem;
	}
}

@keyframes gotozero {
	0% {
		width: 100%;
	}
	100% {
		width: 0%;
	}
}
