@use '../../../consts.scss' as *;

.bg {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	background-color: rgba(0, 0, 0, 0.4);
	z-index: 1000;
	.box {
		display: flex;
		flex-direction: column;

		padding: 1rem;

		background-color: $bg;
		border-radius: 1rem;
		filter: drop-shadow(0 0 5px #000);
	}
}
