@use '../../../consts.scss' as *;

.header {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;

	margin: 0.5rem;
	height: 5rem;

	background-color: darken($bg, 10);
	border-radius: 1rem;
	overflow: hidden;
	.section {
		display: flex;
		flex-direction: row;
		align-items: center;
		margin: 0.5rem;
	}
	.logo {
		height: 4rem;
		margin: 0.5rem;

		border-radius: 100%;
	}
	.name {
		font-size: 200%;
	}
}
