@use '../../../../consts.scss' as *;

.settings-page .box {
	align-items: flex-end;
	h1 {
		width: 100%;
	}
	.list {
		display: flex;
		flex-direction: column;
		.setting {
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: space-between;
			.info {
				max-width: 20rem;
				.name {
					font-size: 150%;
				}
				.desc {
					font-size: 90%;
					opacity: 0.8;
				}
			}
			.num-input {
				font-size: 150%;
				width: 5rem;
				text-align: right;
			}
		}
	}
}
